import React, { useState, useEffect, Component } from "react";
import { Link } from "react-router-dom";
import { Button } from "./Button";
import M from "materialize-css";
import "./navbar.css";
import firebase from "../Firebase";

class Navbar extends Component {
  constructor(props) {
    super(props);
    firebase.auth().onAuthStateChanged((user) => {
        this.forceUpdate();
      
    });
  }
  handleLogOut = () => {
    firebase.auth().signOut();
  };
  componentDidMount() {
    M.Sidenav.init(this.sidenav,[]);
  }
  render() {
    return (
      <nav className="navbar">
        <div className="nav-wrapper">
          <a href="/" className="brand-logo center">
            <img
              src="../images/logo.png"
              className="logonav"
              title="Logo de Nytuo"
            />
          </a>
          <a href="#" data-target="mobile-demo" className="sidenav-trigger">
            <i className="material-icons">menu</i>
          </a>
          <ul className="right hide-on-med-and-down">
            <li>
              <div class="chip">
                {firebase.auth().currentUser ? (<img
                  className="NcircleimgC"
                  src={firebase.auth().currentUser.photoURL}
                  alt="profile picture"
                />):(<></>)}
                

                {firebase.auth().currentUser ? (firebase.auth().currentUser.displayName):("Non Connecter") }
              </div>
             {firebase.auth().currentUser ? (<button
              style={{margin: "5px" }}
            className="btn waves-effect waves-light"
            onClick={this.handleLogOut}
          >
            Se dconnecter
          </button>):(<>
            <a
        class="waves-effect waves-light btn modal-trigger"
        data-target="modal1"
      >
        Formulaire de connexion
      </a>
          </>)} 
            </li>
          </ul>
          <ul id="nav-mobile" className="left hide-on-med-and-down">
            <li>
              <Link to="/">Accueil</Link>
            </li>
            <li>
              <Link to="/games">Jeux</Link>
            </li>
            <li>
              <a href="/softwares">Logiciels</a>
            </li>
            <li>
              <a href="https://github.com/Nytuo">GitHub</a>
            </li>
            <li>
              <a href="/profile">Profile</a>
            </li>
            <li>
              <a href="/store">Boutique</a>
            </li>
            <li>
              <a href="/feedback">FeedBack</a>
            </li>
            <li>
              <Link to="/about">A Propos</Link>
            </li>
          </ul>
        </div>
        <ul className="sidenav" id="mobile-demo" ref={sidenav=>{this.sidenav = sidenav;}}>
        <li>
              <a href="/">Accueil</a>
            </li>
            <li>
              <a href="/games">Jeux</a>
            </li>
            <li>
              <a href="/softwares">Logiciels</a>
            </li>
            <li>
              <a href="https://github.com/Nytuo">GitHub</a>
            </li>
            <li>
              <a href="/profile">Profile</a>
            </li>
            <li>
              <a href="/store">Boutique</a>
            </li>
            <li>
              <a href="/feedback">FeedBack</a>
            </li>
            <li>
              <a href="/about">A Propos</a>
            </li>
            <li>
              <div class="chip">
                {firebase.auth().currentUser ? (<img
                  className="NcircleimgC"
                  src={firebase.auth().currentUser.photoURL}
                  alt="profile picture"
                />):(<></>)}
                

                {firebase.auth().currentUser ? (firebase.auth().currentUser.displayName):("Non Connecter") }
              </div><br/>
             {firebase.auth().currentUser ? (<button
              style={{margin: "5px" }}
            className="btn waves-effect waves-light"
            onClick={this.handleLogOut}
          >
            Se dconnecter
          </button>):(<>
            <a
        class="waves-effect waves-light btn modal-trigger"
        data-target="modal1"
      >
        Formulaire de connexion
      </a>
          </>)} 
            </li>
        </ul>
      </nav>
    );
  }
}

export default Navbar;
