import React, { useEffect } from "react";
import M from "materialize-css";
import Footer from "../Footer";

function Help() {
  useEffect(() => {
    document.addEventListener("DOMContentLoaded", function () {
      var elems = document.querySelectorAll(".collapsible");
      var instances = M.Collapsible.init(elems, []);
    });
  }, []);
  return (
    <div>
      <h1>Page d'aide Nytuo (Site, Jeux, Launcher) :</h1>
      <p>
        Cette page permet de vous aider en cas de problme avec le Nytuo
        Launcher, le site ou mme les jeux.
        <br />
        Cette page est suceptible  des changements.
      </p>
      <h1>Nytuo Launcher :</h1>
      <ul class="collapsible">
        <li>
          <div class="collapsible-header">Avertissements</div>
          <div class="collapsible-body">
            <span>
              <p>
                Le Nytuo Launcher est souvent dtecter par les antivirus comme
                un virus car il modifie les dossiers et les fichiers du
                Launcher.Vous pouvez vrifier les fichiers avec Virus Total qui
                permet lanalyse sur plusieurs base de donnes.Si vous souhaiter
                utilis le launcher mais que votre antivirus le dtecte, il faut
                restaur les fichiers en quarantaine et mettre le dossier
                gnrale en exclusion danalyse et de lancement. Paramtres
                diffrents entre les antivirus.
              </p>
            </span>
          </div>
        </li>
        <li>
          <div class="collapsible-header">
            Tlchargement, installation et lancement
          </div>
          <div class="collapsible-body">
            <span>
              <h1>Windows : </h1>
              <p>
                I/ En cliquant sur le boutton 'Tlcharger le Nytuo Launcher'
                sur la page du site concernant le Nytuo-Launcher, Tlcharger
                sur Github le fichier vous convenant.
              </p>
              <p>
                Note : Pour une installation calssique sur votre ordinateur,
                choisissez, le "Nytuo-Launcher-Setup-[dernire version].exe",
                pour une version portable choisissez,le
                "Nytuo-Launcher-[dernire version]-win.zip" et allez voir la
                section "Portable Edition"
              </p>
              <p>(Uniquement pour ce qui ont choisi le ".exe" )</p>
              <p>II/Lancer l'installeur, toutes est automatique!</p>
              <p>
                III/Windows : Aprs l'installation, le Nytuo launcher s'ouvre
                automatiquement. Un raccourcie  galement tait cr sur le
                bureau.
              </p>
              <p>
                Lors du premier lancement du launcher vous serez inviter 
                selectionner un dossier o stocker les jeux (ce dossier doit
                tre ddier et exclusif. Il peut provenir d'une autre
                installation et dj contenir les jeux(en respectant la
                hirarchisation)). Vous devrez galement choisir une langue
                (Franais, Anglais ou Espagnol)
              </p>
              <h1>Linux : </h1>
              <p>
                I/ En cliquant sur le boutton 'Tlcharger le Nytuo Launcher'
                sur la page du site concernant le Nytuo-Launcher, Tlcharger
                sur Github le fichier vous convenant.
              </p>
              <p>
                Note : Pour une utilisation classique sur votre ordinateur,
                choissisez,"Nytuo-Launcher-Setup-[dernire
                version].AppImage".Pour une version portable,
                choisissez,"nytuolauncher-[dernire version].zip" et allez voir
                la section "Portable Edition"{" "}
              </p>
              <p>
                II/Clique droit sur le ".appimage" puis "proprits".Cocher dans
                "permissions" la case "Executer comme une application".Double
                cliques sur le fichier ".appimage" pour l'executer.
              </p>
              <p>
                Lors du premier lancement du launcher vous serez inviter 
                selectionner un dossier o stocker les jeux (ce dossier doit
                tre ddier et exclusif. Il peut provenir d'une autre
                installation et dj contenir les jeux(en respectant la
                hirarchisation)). Vous devrez galement choisir une langue
                (Franais, Anglais ou Espagnol)
              </p>
            </span>
          </div>
        </li>
        <li>
          <div class="collapsible-header">Dossier des jeux</div>
          <div class="collapsible-body">
            <span>
              <p>
                Pour que vous soyez plus libre, le dossier des jeux est
                dynamique, c'est  vous de le choisir lors de la premire
                utilisation ou dans les paramtres pour le changer.Il peut
                provenir d'une autre installation et dj contenir les jeux(en
                respectant la hrarchisation (Games\[ID]\[Game files]))).
              </p>
            </span>
          </div>
        </li>
        <li>
          <div class="collapsible-header">Backup des jeux</div>
          <div class="collapsible-body">
            <span>
              <p>
                Dans le cadre d'une sauvegarde des jeux manuellement,
                <br />
                I/Dans les paramtres se trouve l'emplacement du dossier "Games"
                contenant les jeux.Suivez cette emplacement dans l'explorateur
                de fichier.
                <br />
                II/ Copier et coller le dossier "Games" ailleurs sur votre
                machine. <br />
                Note: Le dossier "Games" se trouve en cas d'erreur dans vos
                Documents sur Linux ou au dossier prcdent le dossier du
                Nytuo-Launcher sur Windows.Ce dossier tant attribu
                dynamiquement (c'est  vous de dterminer son emplacement lors
                de la premire utilisation), il est difficile de pouvoir vous
                aider plus...
              </p>
            </span>
          </div>
        </li>
        <li>
          <div class="collapsible-header">Mettre  jour le Nytuo Launcher</div>
          <div class="collapsible-body">
            <span>
              <p>
                Sur Windows: Si une mise  jour est disponible, le Nytuo
                Launcher la tlcharge au dmarrage, se ferme et lance un Batch
                (fichier en .bat lanant un invite de commandes (CMD)) de
                manire  lancer le programme de mise  jour de manire diffr
                et que le launcher puisse se fermer correctement.
                <br />
                Sur Linux: La mise  jour, si disponible se tlcharge et sera
                accessible directement sur le bureau Linux. Vous pourrez alors
                la dplac et mme supprimer l'autre version manuellement
                <br />
                Note : Vous pouvez forcer la mise  jour du launcher depuis les
                paramtres
                <br />
                Note II : en mode portable, les mises  jour sont dsactiver, le
                guide pour mettre  jour le launcher en mode portable se trouve
                dans la section "portable".
              </p>
            </span>
          </div>
        </li>
        <li>
          <div class="collapsible-header">Dinstallation</div>
          <div class="collapsible-body">
            <span>
              <p>
                Sur Windows: <br />
                <br />
                La dinstallation se fait en trois temps:
                <br />
                (Seulement si vous souhaiter galement supprimer les jeux)
                <br />
                Aller dans le Nytuo Launcher, dans les paramtres, regard la
                ligne concernant l'emplacement du dossier des jeux.Fermer le
                launcher et aller  cette emplacement et supprimer-le.
                <br />
                I/Dinstaller le logiciel comme n'importe qu'elle autres, dans
                les paramtres.
                <br />
                II/Dans le dossier "Local" de "Appdata", trouver le dossier
                "nytuolauncher" et "nytuolauncher_data" et supprimer les.
                <br />
                III/Vous pouvez effectuer un redmarrage au besoin
                <br />
                Vous venez de supprimer le Launcher de votre machine
                <br />
                <br />
                Sur Linux :<br />
                (Seulement si vous souhaiter galement supprimer les jeux)
                <br />
                Aller dans le Nytuo Launcher, dans les paramtres, regard la
                ligne concernant l'emplacement du dossier des jeux.Fermer le
                launcher et aller  cette emplacement et supprimer-le.
                <br />
                I/Supprimer le .appimage du Nytuo Launcher
                <br />
                II/Supprimer dans le dossier "Documents", le dossier
                "nytuolauncher_data".
                <br />
                IV/Redmarrer l'ordinateur pour enlever toutes traces local.
                <br />
                Vous venez de supprimer le Launcher de votre machine
                <br />
                <br />
                Pour les versions Portable :<br />
                Mettez le dossier contenant les jeux, le launcher et le
                nytuolauncher_data  la corbeille et videz-l.
              </p>
            </span>
          </div>
        </li>
        <li>
          <div class="collapsible-header">Portable Edition</div>
          <div class="collapsible-body">
            <span>
              <p>
                Le Nytuo Launcher existe en version portable. Elle ne ncessite
                donc pas d'installation.Cependant, les mises  jour automatiques
                sont dsactivs et le dossier des jeux ne peut tre choisi.
              </p>
              <p>
                I/Pour obtenir la version portable du launcher, tlcharger
                depuis GitHub le zip : "Nytuo-Launcher-[dernire
                version]-win.zip" pour windows et le zip :
                "nytuolauncher-[dernire version].zip" pour Linux.O [dernire
                version] correspond  la dernire version du launcher.
              </p>
              <p>
                II/Crer un dossier "Nytuo" pour accueillir le tout (le nom de
                ce dossier n'a pas d'importance mais c'est logique).Dans ce
                dossier crer un dossier "Nytuo-Launcher"(encore une fois, le
                nom n'a pas d'importance) puis extrayer le contenu de l'archive
                dans ce dossier.
              </p>
              <p>
                III/Aller dans le dossier "Nytuo-Launcher" puis aller dans :
                "Resources/app/", ouvrez le fichier "portable.txt" et modifier
                le "false" en "true".Cela permet d'activer les fonctionnalits
                portable. Sinon le launcher fonctionnera mais les mises  jour
                causeront problmes et le dossier des jeux ne sera pas portable.
              </p>
              <p>
                IV/Par la suite vous avez juste, dans le dossier
                "Nytuo-Launcher" a lanc le "Nytuo-Launcher.exe"
              </p>
              <p>
                Pour les mises  jour, supprimer le contenu du dossier
                "Nytuo-Launcher" (mais pas le dossier) et refaite les tapes
                depuis la I sans cr de nouveau dossiers et en utilisant ceux
                existants.{" "}
              </p>
              <p>
                Pour dinstaller le launcher, vous devez mettre le dossier
                "Nytuo"  la corbeille et la vider, si vous voulez garder les
                jeux ne supprimer que les dossier "Nytuo-Launcher" et
                "nytuolauncher_data".
              </p>
              <p>
                Note : Si vous devez dplacer le logiciel, vous devez dplacer
                le dossier "Nytuo" en entier.
                <br />
                Note II : le launcher va cr deux nouveaux dossiers dans le
                dossier "Nytuo".Un pour les jeux, l'autre pour les paramtres
                externes.
              </p>
            </span>
          </div>
        </li>
        <li>
          <div class="collapsible-header">En cas d'erreurs</div>
          <div class="collapsible-body">
            <span>
              <p>
                Visiter la section "FeedBack" du site ou rendez-vous sur GitHub
                et cr une nouvelle "issue"
              </p>
              <p>Je corrige les bugs quand je les connais au plus vite...</p>
            </span>
          </div>
        </li>
        <li>
          <div class="collapsible-header">Notes</div>
          <div class="collapsible-body">
            <span>
              <p>
                I/ Sous Linux, pendant l'execution d'un jeu le launcher ne
                fonctionne pas ! Vous devez fermer le jeu pour retrouver l'accs
                au launcher.
              </p>
              <p>
                II/ Sous Linux, Pour lancer le launcher dans le cas o, celui-ci
                ne ce lance pas, veuillez  cocher la case dans l'onglet
                "permissions" des "proprits" du fichier et ressayer.Si
                l'erreur persiste, envoyer un feedback.
              </p>
            </span>
          </div>
        </li>
      </ul>
      <h1>Gnrale :</h1>
      <ul class="collapsible">
        <li>
          <div class="collapsible-header">Comptes</div>
          <div class="collapsible-body">
            <span>
              <p>
                Depuis la mise  jour 2.8.2 du launcher, un compte est
                ncessaire pour accder aux pages des jeux sur ce dernier. Cela
                me permet de grer d'ventuelles jeux payant, de m'entrainer 
                grer des bases de donnes et  apprendre le language 'PHP', et
                me permet d'inclure dans mes jeux un nom d'utilisateur
                customis. Pour cr un compte, ce qui est gratuit, aller sur la
                page profile du site ou ouvrer le Nytuo Launcher. Cliquer sur
                'cr un compte'  cot de 'se connecter' et renseigner les
                champs requis. Noter que le mot de passe doit-etre facile a
                retenir en cas de reconnexion mais doit aussi etre unique a ce
                site en cas de hack de la base de donne; il doit galement tre
                assez complexe pour ne pas etre deviner. Pour vous connecter,
                cela ce fait sur la page profile du site. Si vous perdez votre
                mot de passe, un systeme de mail a tait mis au point, vous
                receverez un mail avec un lien de rinitialisation de mot de
                passe votre compte.
                <br />
                Note: Je n'ai moi-mme pas accs a vos mot de passes car ils
                sont crypts dans la base de donnes.
                <br />
                Une fois connect, rien ne vous empeche de pourvoir changer
                votre mot de passe de la mme manire.Ainsi que votre nom
                d'utilisateur. Vous ne pouvez pas modifier votre Identifiant
                Unique, ni changer de mail (sauf cas dment justifier, en me
                contactant par la catgorie 'FeedBack du site'). Dans votre
                profile, vous pouvez voir les jeux possder sur votre compte et
                pouvez en activer un  l'aide d'une cl qui vous sera envoy par
                mail si besoin (dans le cadre de jeux payant, les jeux gratuits
                seront automatiquement activs). Votre compte peut-tre
                galement utilis dans le Nytuo Launcher pour que vous puissiez
                retrouver votre Nom d'Utilisateur dans les jeux et accder  vos
                jeux.Toutes les fonctionnalits du site li aux comptes sont
                partags avec le launcher et sont galement disponible sur ce
                dernier. Si vous cochez l'option 'se souvenir de moi', le mot de
                passe est stock dans un cookie ( chiffr ), ainsi que votre nom
                d'utilisateur, pour vous permettre une reconnexion plus simple.
                Sur le Launcher, votre mot de passe est stock dans un fichier
                txt sur votre machine, il n'est cependant pas scuris.
              </p>
            </span>
          </div>
        </li>
      </ul>
      <h1>Questionnements :</h1>
      <p>Visiter la section "FeedBack" du site</p>
      <Footer />
    </div>
  );
}

export default Help;
